@import url(https://fonts.googleapis.com/css?family=Poiret+One);
html {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 2;
  max-width: 1350px;
  width: 100%;
  margin: auto;
}

body {
  padding: 10px;
  padding-bottom: 100px;
  position: relative;
  background-color: white;
  overflow-x: auto;
  overflow-y: auto;

}

header {
  height: 150px;
}

h1 {
  font-size: 50px;
  line-height: 140px;
  /* margin-left: 70px; */
  margin-left: 10%;
}

h2 {
  margin-left: 65px;
  font-size: 35px;
}

h3 {
  margin-bottom: 10px;
  font-size: 30px;
  text-indent: 40px;
}

.dropdown-menu {
overflow-y: auto;
overflow-x: hidden;
}

.content_container {
  /* margin: 20px 20px 50px 0; */
  text-indent: 30px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  /* border: 1px solid black; */
}

.self_intro {
  width: 480px;
  font-size: 20px;
  text-indent: 20px;
  padding-bottom: 20px;
  padding-top: 15px;
  font-size: 20px;
  line-height: 2;
} 

#cat_pic_about_me{
  display: inline-block;
  max-width: 100%;
  margin-right: 40px;
}

.self-info {
  display: inline-block;
}


.socials{
  padding: 10px;
}

.socials a {
  margin: 10px 15px 10px 10px;
}

@media (max-width: 1240px) {

  html {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 2;
    min-width: 600px;
    width: 100%;
    margin: auto;
  }

  body {
    padding-left: 50px;
    padding-right: 50px;
    position: relative;
    background-color: white;
    overflow-x: auto;
    overflow-y: auto;
  }

  h1 {
    font-size: 40px;
  }

  #cat_pic_about_me{
    display: none;
  }
  
  .self-info {
    margin-top: 5%;
  }

  .h2-tools {
    font-size: 26px;
  }

}


.topnav {
  font-size: 18px;
  font-family: "Helvetica Neue",sans-serif;
  border-bottom: rgb(211, 211, 211);
  border-bottom-style: solid;
  border-bottom-width: 1px;
  }
  
  .topnav a{
    margin-left: 30px;
  }


.dd_item {
  margin: 0px !important;
  font-size: 17px !important;
  color: gray;
}

.dd_item:hover{
  background-color: transparent !important;
}

.weather-widget {
margin-top: 5%;
display: flex; 
justify-content: center; 
}

#weather_widget {
font-family: 'Poiret One';
display: flex;  
height: 300px;
width: 600px;
flex-wrap: wrap;
cursor: pointer;
}

#weather_widget .weatherIcon {
flex: 1 1 100%;
height: 60%;
border-top-left-radius: 20px;
border-top-right-radius: 20px;
background: #FAFAFA;
display: flex;
align-items: center;
justify-content: space-around;
}

#weather_widget .weatherInfo {
flex: 0 0 70%;
height: 40%;
background: #080705;
border-bottom-left-radius: 20px;
display: flex;
align-items: center;
color: white;
}

#weather_widget .weatherInfo .temperature {
flex: 0 0 40%;
width: 100%;
font-size: 65px;
display: flex;
justify-content: space-around;
}

#weather_widget .weatherInfo .description {
flex: 0 1 60%;
display: flex;
flex-direction: column;
width: 100%;
height: 100%;
justify-content: center;
}

#weather_widget .weatherInfo .description .weatherCondition {
text-transform: uppercase;
font-size: 35px;
font-weight: 100;
}

#weather_widget .weatherInfo .description .place {
font-size: 15px;
}

#weather_widget .date {
flex: 0 0 30%;
height: 40%;
background: #70C1B3;
border-bottom-right-radius: 20px; 
display: flex;
justify-content: space-around;
align-items: center;
color: white;
font-size: 30px;
font-weight: 800;
}

#weather_widget p {
position: fixed;
bottom: 0%;
right: 2%;
}

#weather_widget p a {
text-decoration: none;
color: #E4D6A7;
font-size: 10px;
}

.ww-user-input-container {
display: flex;
justify-content: center;
}

.ww_user_input {
width: 600px;
font-size: 16px;
margin: 30px 0 0 20px;
}

#ww_user_input_city {
width: 80%;
border:2px solid #aaa;
border-radius:4px;
margin:8px 0;
outline:none;
padding:8px;
box-sizing:border-box;
transition:.3s;
}
  
  
#ww_user_input_city:focus{
/* border-color:dodgerBlue;
box-shadow:0 0 8px 0 dodgerBlue; */
border-color:rgb(170, 170, 170);
box-shadow:0 0 8px 0 rgb(170, 170, 170);
}

#search_ww {
margin: 8px;
padding: 8px;
background-image: url(/ReactPlayground/static/media/search_icon.f7a26ab2.svg);
background-size: cover;
width: 30px;
height: 30px;
border-radius: 4px;
background-color: white;
border: none;
}

#ww-spinner {
margin-left: 50%;
margin-top: 10%;
}

.h2-tools {
/* margin-top: 40px;
margin-bottom: 40px; */
margin-top: 3%;
margin-bottom: 3%;
margin-left: 8%;
display: inline-block;
}

.widget-i {
background-image: url(/ReactPlayground/static/media/i-letter.8f30f182.svg);
background-color: white;
width: 17px;
height: 17px;
border: none;
display: inline-block;
margin-left: 5px;
transform: translate(0, -10px);
}

.search-bar-ce {
margin-left: 15px;
margin-right: 10px;
padding: 0 0 5px 5px;
}

.ce-search-bar-group{
position: -webkit-sticky;
position: sticky;
background: white;
top: 0%;
}

.input-search-bar-ce {
margin-left: 5px;
width: 350px;
}

.filter-icon {
padding: 2px;
margin-right: 5px;
margin-bottom: 5px;
}

.ce-input-box {
width: 50%;
padding: 10px 10px 10px 0;
margin-left: 25%;
}

.input-box:focus {
border-color:rgb(170, 170, 170);
box-shadow:0 0 8px 0 rgb(170, 170, 170);
}

.ce-dropdown-from {
display: inline-block; 
}

.ce-dropdown-to {
display: inline-block;
margin-left: 10%;
}

.ce-dropdown-group {
  display: flex;
  justify-content: center;
  line-height: 40px;
}

.ce-dropdown-menu {
    max-height: 300px;
    max-width: 1000px;
    min-width: 500px;
  }

.ce-dropdown-custom-1 {
  width: 123px;
}

.ce-dropdown-custom-2{
  width: 140px;
}

.ce-button-convert {
  margin: 3% 0 3% 25%
}

.ce-button-clear {
  margin-left: 2%
}

